:root {
  --font-martel: "Martel Sans", sans-serif;
}

:root {
  --cream-white: hsl(60, 20%, 96%);
  --virgin-green: hsl(138, 53%, 85%);
  --light-blue: hsl(191, 48%, 82%);
  --pale-green: hsl(184, 34%, 58%);
  --deep-green: hsl(185, 41%, 37%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  height: 100%;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-martel);
}

#particles-js {
  position: fixed;
  top: 9.375rem;
  left: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

h1 {
  margin-block: auto;
}

header {
  height: 10rem;
}
header #bg-box {
  background-image: url("/images/bidhan_front.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  filter: blur(10px);
  height: inherit;
}
header #text-box {
  background-color: black;
  /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4);
  color: var(--cream-white);
  border: 2px solid var(--cream-white);
  font-weight: bold;
  position: absolute;
  z-index: 1;
  top: 1%;
  width: 100%;
  display: grid;
  grid-template-columns: 20% 80%;
  column-gap: 2.5rem;
}
header #text-box h1 {
  font-size: 3.4375rem;
}
header #text-box h1,
header #text-box #name_footer {
  justify-self: start;
}
header #text-box img,
header #text-box #icon_footer {
  justify-self: end;
}
header #text-box #icon_footer {
  padding-right: 10px;
}
@media (max-width: 71.8125em) {
  header #text-box {
    column-gap: 0.625rem;
    grid-template-columns: 15% 85%;
  }
  header #text-box h1 {
    font-size: 2.5rem;
  }
  header #text-box #icon_footer,
header #text-box #name_footer {
    display: none;
  }
  header #text-box h1,
header #text-box #name_footer,
header #text-box img,
header #text-box #icon_footer {
    justify-self: center;
  }
}
@media (max-width: 39.9375em) {
  header #text-box {
    display: inherit;
  }
  header #text-box h1 {
    font-size: 1.875rem;
  }
  header #text-box img {
    display: none;
  }
}
@media (max-width: 39.9375em) {
  header {
    height: 4.375rem;
  }
}

nav {
  background-color: var(--deep-green);
  height: 7vh;
  margin-top: 0.125rem;
}
nav .nav-links {
  display: flex;
  position: relative;
  margin: auto;
  width: 80%;
  height: 60%;
  justify-content: space-evenly;
  align-items: center;
  list-style: none;
  z-index: 2;
  top: 11px;
}
nav .nav-links a {
  color: var(--pale-green);
  text-decoration: none;
  font-size: 1.25rem;
}
nav .nav-links a:before {
  position: absolute;
  color: var(--cream-white);
  overflow: hidden;
  content: attr(alt);
  transition: all 0.5s;
  transform: scale(1.2);
  opacity: 0;
}
nav .nav-links a:hover:before {
  transform: scale(1);
  opacity: 1;
}